* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

a {
    text-decoration: inherit;
    color: inherit;
}

img {
    display: block;
    border: 0;
}

header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: fixed;
    z-index: 99;
    top: 0;
    width: 100%;
    padding: 0 20px;
    text-align: center;
    background-color: purple;
}

header>form {
    margin: 10px auto;
}

.gallery {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    margin-top: 92px;
    margin-bottom: 24px;
    padding: 0 42px;
}

.photo-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    width: calc((100% - 72px) / 4);
    margin-right: 24px;
    margin-bottom: 24px;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
}

.photo-card:nth-child(4n + 4) {
    margin-right: 0;
}

.photo-card img {
    height: 100%;
    object-fit: cover;
}

.photo-card .info {
    display: flex;
}

.info-item {
    padding: 8px 0;
    font-size: 1vw;
    text-align: center;
}

.end-of-gallery {
    display: block;
    width: 100%;
    text-align: center;
    font-size: 12px;
}

.load-more {
    display: block;
    margin: 0 auto 42px;
    width: 250px;
    height: 42px;
    font-size: 15px;
    color: white;
    background-color: steelblue;
    box-shadow: none;
    border: 1px solid black;
    border-radius: 4px;
}

.load-more:active {
    box-shadow: inset 2px 2px 4px rgba(0, 0, 0, 0.25);
}